  border-radius: 3px;
  flex-direction: column;
  gap: 15px;
  transition: all .4s cubic-bezier(.25, .46, .45, .94) .1s;
  display: flex;
  position: absolute;
  top: 80%;
  left: 0%;
  transform: translateY(100px);
  box-shadow: 0 3px 8px rgba(0, 0, 0, .24);
}

.sub-menu-item {
  padding-left: 10px;
}

.sub-menu-item:first-child {
