}

.sub-menu-item:last-child {
  margin-bottom: 15px;
}

.sub-menu-item:hover a {
  color: gray;
  transition: all .25s ease-in;
}

.sub-menu .menu-item {
  display: block;
}

.menu-item:hover .sub-menu {
  visibility: visible;
